.page {
  font-family: "Poppins", sans-serif;
  color: $white-main-accent;
  font-size: 16px;
  line-height: 140%;
  font-weight: 400;
  scroll-behavior: smooth;

  &__body {
    min-height: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transition: all $transition-time;
    opacity: 0;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}
